t-1 {\r\n    top: 85px;\r\n  }\r\n  div.skeleton-content-2 {\r\n    top: 105px;\r\n  }\r\n  div.skeleton-content-3 {\r\n    top: 125px;\r\n  }\r\n  \r\n  @-webkit-keyframes placeholderSkeleton {\r\n    0% {\r\n      background-position: -468px 0;\r\n    }\r\n    100% {\r\n      background-position: 468px 0;\r\n    }\r\n  }\r\n  \r\n  @-webkit-keyframes skeletonAnimate {\r\n    from {\r\n      background-position: top left;\r\n    }\r\n    to {\r\n      background-position: top right;\r\n    }\r\n  }\r\n\r\n  .data_loader{\r\n      min-height: 100px;\r\n      display: flex;\r\n      align-items: center;\r\n      justify-content: center;\r\n    &:after{\r\n        content:'';\r\n        border: 5px solid #ffcecf;\r\n        border-radius: 50%;\r\n        border-top: 5px solid #d8141c;\r\n        width: 40px;\r\n        height: 40px;\r\n        -webkit-animation: spin 1s linear infinite; /* Safari */\r\n        animation: spin 1s linear infinite;\r\n    }\r\n    &.full-inside{\r\n        background: #ecedef;\r\n        height: calc(100vh - 160px);\r\n        position: fixed;\r\n        top: 100px;\r\n        width: 100vw;\r\n        z-index: 5;\r\n    }\r\n    &.full-page{\r\n      background: #ecedef;\r\n        height:100vh;\r\n        position: fixed;\r\n        top: 0;\r\n        width: 100vw;\r\n        z-index: 5;\r\n    }\r\n  }\r\n\r\n  /* Safari */\r\n@-webkit-keyframes spin {\r\n    0% { -webkit-transform: rotate(0deg); }\r\n    100% { -webkit-transform: rotate(360deg); }\r\n  }\r\n  \r\n  @keyframes spin {\r\n    0% { transform: rotate(0deg); }\r\n    100% { transform: rotate(360deg); }\r\n  }\r\n  \r\n  .icon{\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n  }\r\n\r\n  .substitute_out,.own-goal{\r\n    fill: #f34235 !important;\r\n  }\r\n  .substitute_in,.penalty_goal{\r\n    fill: #4bae4f !important;\r\n  }\r\n\r\n  .mt15{\r\n    margin-top: 15px;\r\n    display: block;\r\n  }\r\n\r\n  .post-card--single .post-card__da